/* Colors */
/*
dark blue: #476A6F
light blue: #258EA6
dark green: #519E8A
medium green: #7EB09B
light green: #A3C4BC
*/

.wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #258ea6;
}

.form-wrapper {
  width: 400px;
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
  border-radius: 10px;
  box-shadow: 0px 10px 50px #555;
  background-color: #ffffff;
}

form {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

h1 {
  text-align: center;
  width: 100%;
  color: #111;
  font-weight: lighter;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

label {
  font-size: 0.8em;
  margin-bottom: 0.25em;
  color: #222;
  font-weight: lighter;
}

input {
  padding: 10px 10px;
  border-radius: 5px;
  outline: none;
  border: 1px solid #cfcfcf;
}

input::-webkit-input-placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input:-ms-input-placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input::-ms-input-placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input::placeholder {
  font-size: 1.2em;
  font-weight: lighter;
  color: #999;
}

input.error {
  border: 1px solid red;
}

.errorMessage {
  color: red;
  font-size: 0.75em;
  display: relative;
}

.userName {
  margin-right: 1%;
}

.userName,
.email,
.password {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.userName {
  width: 100%;
}

.email,
.password {
  width: 100%;
}

.createAccount {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.createAccount button {
  background-color: #519e8a;
  color: #fff;
  border: 2px solid #fff;
  width: 100%;
  margin-top: 1em;
  padding: 8px 0px;
  font-size: 1em;
  font-weight: lighter;
  letter-spacing: 1px;
  margin-bottom: 0.25em;
}

.createAccount button:hover:not([disabled]) {
  color: #519e8a;
  background-color: #fff;
  border: 2px solid #519e8a;
}

.createAccount small {
  color: #999;
  font-weight: lighter;
}
.box {
  background-color: #ffffff;
  border-radius: 10px 10px 10px 10px;
  overflow: hidden;
  /* height: 200px; */
  max-height: 35vh;
  margin: 20px;
  box-shadow: 0px 12px 18px -6px rgba(0, 0, 0, 0.3);
}


.grid {
  min-height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.grid > div {
  flex-basis: calc(25% - 40px);
}


.boxy {
    width: 300px;
    background: green;
    padding: 50px;
    margin-left: auto;
    margin-right: auto;
  }

  .boxy:hover {
    -webkit-animation: boxyRotation 4s infinite linear;
            animation: boxyRotation 4s infinite linear;
  }
  
  @-webkit-keyframes boxyRotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }
  
  @keyframes boxyRotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }
  
  .black-background {
    background-color: red !important;
    font-size: 5vh;
    border-radius: 10px 10px 10px 10px;
  }

  .fiximgsize{
    height:  '40%'
  }

